.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  display: none;
}

.cat-info {
  max-width: 600px;
  text-align: center;
  margin: 0 auto;
  padding-top: 50px;
}

.cat-image {
  width: 100%;
  max-height: 300px;
  object-fit: cover;
  border-radius: 8px;
}

.cat-name {
  margin-top: 16px;
  font-size: 24px;
  font-weight: bold;
}

.cat-description {
  margin-top: 8px;
  font-size: 16px;
}

.cat-temperament {
  color: gray;
  margin-top: 8px;
  font-size: 14px;
}

